@font-face {
  font-family: "Rubik";
  src: url("./assets/fonts/Rubik-Regular.eot");
  src: url("./assets/fonts/Rubik-Regular.eot?#iefix") format("embedded-opentype"), url("./assets/fonts/Rubik-Regular.woff2") format("woff2"), url("./assets/fonts/Rubik-Regular.woff") format("woff"), url("./assets/fonts/Rubik-Regular.ttf") format("truetype"), url("./assets/fonts/Rubik-Regular.svg#Rubik-Regular") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Yananeska";
  src: url("./assets/fonts/Yananeska.eot");
  src: url("./assets/fonts/Yananeska.eot?#iefix") format("embedded-opentype"), url("./assets/fonts/Yananeska.woff2") format("woff2"), url("./assets/fonts/Yananeska.woff") format("woff"), url("./assets/fonts/Yananeska.ttf") format("truetype"), url("./assets/fonts/Yananeska.svg#Yananeska") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
html, body {
  scrollbar-width: thin;
  /* "auto" or "thin" */
  scrollbar-color: #5c2a92ff #000;
  /* scroll thumb and track */
  max-height: 100vh;
  min-height: 100vh;
  max-width: 100vw;
  min-width: 100vw;
  display: grid;
  grid-template-areas: "body";
  grid-template-rows: 100vh;
  grid-template-columns: 100vw;
  overflow: hidden;
  margin: 0px;
  padding: 0px;
  font-family: "Rubik";
  font-size: 14px;
}
html ::-webkit-scrollbar, body ::-webkit-scrollbar {
  border-color: #000;
  border-radius: 20px;
  /* roundness of the scroll thumb */
  width: 12px;
  /* width of the entire scrollbar */
}
html ::-webkit-scrollbar-track, body ::-webkit-scrollbar-track {
  border-color: #000;
  border-radius: 20px;
  /* roundness of the scroll thumb */
  background: #000;
  /* color of the tracking area */
}
html ::-webkit-scrollbar-thumb, body ::-webkit-scrollbar-thumb {
  background: #ff2cf3ff;
  /* Old browsers */
  background: -moz-linear-gradient(top, #ff2cf3ff 0%, #2ce4d8ff 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #ff2cf3ff 0%, #2ce4d8ff 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #ff2cf3ff 0%, #2ce4d8ff 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#89ffb0", endColorstr="#98dcfb",GradientType=0 );
  /* IE6-9 */
  border-radius: 20px;
  /* roundness of the scroll thumb */
  border: 3px solid #000;
  /* creates padding around scroll thumb */
}
html ::-webkit-scrollbar-corner, body ::-webkit-scrollbar-corner {
  background: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}
html ::selection, body ::selection {
  color: #ff2cf3ff;
  background: #000;
}
@media (max-width: 400px), (max-height: 600px) {
  html, body {
    font-size: 10px;
  }
}
@media (max-width: 300px), (max-height: 500px) {
  html, body {
    font-size: 8px;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Rubik";
}

h1 {
  font-family: "Yananeska";
  font-weight: 500;
  color: #2ce4d8ff;
  text-shadow: 5px 2.5px 0px #000, 7.5px 7.5px 0px #ff2cf3ff;
  font-size: 8rem;
  margin: 0px;
}
@media (max-width: 800px), (max-height: 1000px) {
  h1 {
    font-size: 5rem;
  }
}

body {
  grid-area: body;
  grid-template-areas: "angular-content";
  background: #000;
  background: url("./assets/backgrounds/bmw.gif");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  backdrop-filter: brightness(0.6);
}

a.icon {
  color: #000;
  transition: 0.4s;
  padding: 2.5px 2.5px;
  border-radius: 4px;
  max-width: fit-content;
  justify-self: center;
  align-self: center;
  position: relative;
}
a.icon:hover.social {
  background: #000;
  color: #5c2a92ff;
}

*.after-info:after {
  transition: 0.4s;
  content: "Empty";
  position: absolute;
  top: 0px;
  right: -40px;
  left: -40px;
  bottom: 0px;
  color: #ff2cf3ff;
  background: #000;
  display: grid;
  padding: 5px;
  opacity: 0;
  border-radius: 4px;
  text-align: center;
  font-size: 1rem;
  align-items: center;
  justify-items: center;
  pointer-events: none;
  min-width: 100%;
  height: fit-content;
  z-index: 999999999;
}
*.after-info:hover:after {
  opacity: 1;
  transform: translateY(-200%);
}
*.after-info:hover.invert:after {
  opacity: 1;
  transform: translateY(200%);
}
*.after-info:hover.invert.short:after {
  opacity: 1;
  transform: translateY(100%);
}

button {
  padding: 0px;
  min-width: 100%;
  max-width: 100%;
  max-height: 50px;
  min-height: 50px;
  border: 2px solid #ff2cf3ff;
  outline: none;
  background: none;
  color: #ff2cf3ff;
  font-family: "Rubik";
  transition: 0.4s;
  border-radius: 4px;
  font-size: 1rem;
  background: rgba(0, 0, 0, 0.4);
  display: grid;
  grid-template-areas: "content";
  align-content: stretch;
  justify-content: stretch;
  position: relative;
  cursor: pointer;
}
button:before {
  transition: 0.4s;
  opacity: 0;
  content: "";
  position: relative;
  min-height: 100%;
  min-width: 100%;
  grid-area: content;
  background: #ff2cf3ff;
  /* Old browsers */
  background: -moz-linear-gradient(top, #ff2cf3ff 0%, #2ce4d8ff 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #ff2cf3ff 0%, #2ce4d8ff 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #ff2cf3ff 0%, #2ce4d8ff 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#89ffb0", endColorstr="#98dcfb",GradientType=0 );
  /* IE6-9 */
  z-index: 1;
}
button:hover label {
  color: #000 !important;
}
button:hover:before {
  opacity: 1;
}
button label {
  align-self: center;
  justify-self: center;
  grid-area: content;
  pointer-events: none;
  color: #ff2cf3ff !important;
  z-index: 2;
}
button.failed {
  background: #f62c4d;
  border-color: #5c2a92ff;
}
button.failed label {
  color: #5c2a92ff !important;
}
button.working {
  background: #ff2cf3ff;
  animation: strobe 4s infinite;
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
}
button.working label {
  color: #000 !important;
}
@keyframes strobe {
  20% {
    border-top-color: #5c2a92ff;
  }
  40% {
    border-right-color: #5c2a92ff;
  }
  50% {
    box-shadow: 0px 0px 4px #5c2a92ff;
  }
  60% {
    border-bottom-color: #5c2a92ff;
  }
  80% {
    border-left-color: #5c2a92ff;
  }
}
button.success {
  background: #3cbb3c;
}
button.success label {
  color: #000 !important;
}
button.greyed {
  color: #5c2a92ff !important;
  border-color: #000;
}
button.greyed label {
  color: #5c2a92ff !important;
}

